<template>
	<view class="tab">
		<scroll-view class="tab-scroll" scroll-x>
			<view class="tab-scroll__box">
				<view
				  v-for="(item, index) in list"
					:key="index"
					@click="tabClick(item, index)"
					:class="{active: index === current}"
					class="tab-scroll__item"
				>
				{{item.name}}
				</view>
			</view>
		</scroll-view>
		<view @click.stop="open" class="tab-icons">
			<uni-icons type="gear" size="26" color="#666666"></uni-icons>
		</view>
	</view>
</template>

<script>
	export default {
	  props: {
			list: {
				type: Array,
				default: () => ([])
			},
			activeIndex: {
				type: Number,
				default: 0
			}
		},
		data() {
			return {
				current: 0
			}
		},
		watch: {
			activeIndex(newVal) {
				this.current = newVal
			}
		},
		methods: {
			tabClick(item, index) {
				this.current = index
				this.$emit('tab', {
					data: item,
					index
				})
			},
			open() {
				uni.navigateTo({
					url: '/pages/home-label/home-label'
				})
			}
		}
	}
</script>

<style lang="scss">
.tab {
	display: flex;
	width: 100%;
  border-bottom: 1px solid #f5f5f5;
	background-color: #FFFFFF;
	box-sizing: border-box;
	&-scroll {
		flex: 1;
		overflow: hidden;
		box-sizing: border-box;
	  &__box {
			display: flex;
			align-items: center;
			flex-wrap: nowrap;
			height: 45px;
			box-sizing: border-box;
			.tab-scroll__item {
				flex-shrink: 0;
				padding: 0 10px;
				font-size: 14px;
				color: #333;
				&.active {
					color: $mk-base-color;
				}
			}
		}	
	}
  
	&-icons {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 45px;
		&::after {
			content: '';
			position: absolute;
			top: 12px;
			bottom: 12px;
			left: 0;
			width: 1px;
			background: #dddddd;
		}
	}
}
</style>
